// AI Suggested responses
const aiSuggestions = [
  "I'll help you resize your image to under 5MB. You can use tools like TinyPNG or simple image editors.",
  "Would you like me to walk you through the process of resizing your image?",
  "I can also explain the other supported file formats if you'd like to try a different image."
];

// Component for the avatar
const Avatar = ({ initials, status }) => {
  const getStatusColor = () => {
    switch (status) {
      case 'active': return 'bg-green-500';
      case 'away': return 'bg-yellow-500';
      case 'inactive': return 'bg-gray-500';
      default: return 'bg-gray-500';
    }
  };

  return (
    <div className="relative">
      <div className="w-10 h-10 rounded-full bg-blue-100 text-blue-600 flex items-center justify-center font-medium">
        {initials}
      </div>
      {status && (
        <div className={`absolute bottom-0 right-0 w-3 h-3 ${getStatusColor()} rounded-full border-2 border-white`}></div>
      )}
    </div>
  );
};

// Main App component
export default function IntercomClone() {
  const [selectedConversation, setSelectedConversation] = useState(null);
  const [showSidebar, setShowSidebar] = useState(true);
  const [isMobile, setIsMobile] = useState(false);
  const [showMobileNav, setShowMobileNav] = useState(false);
  const [newMessage, setNewMessage] = useState('');
  const [messages, setMessages] = useState(dummyMessages);
  const [showAIPanel, setShowAIPanel] = useState(false);
  const [selectedAIResponse, setSelectedAIResponse] = useState(null);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth < 768);
      if (window.innerWidth >= 768) {
        setShowSidebar(true);
        setShowMobileNav(false);
      } else {
        setShowSidebar(false);
      }
    };

    handleResize();
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const handleSelectConversation = (conversation) => {
    setSelectedConversation(conversation);
    if (isMobile) {
      setShowSidebar(false);
    }
  };

  const handleSendMessage = () => {
    if (newMessage.trim() === '') return;
    
    const newMsg = {
      id: messages.length + 1,
      sender: 'agent',
      message: newMessage,
      time: new Date().toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }),
      avatar: 'AG'
    };
    
    setMessages([...messages, newMsg]);
    setNewMessage('');
    setSelectedAIResponse(null);
    setShowAIPanel(false);
  };

  const handleAIResponseSelect = (response) => {
    setNewMessage(response);
    setSelectedAIResponse(response);
  };

  const toggleSidebar = () => {
    setShowSidebar(!showSidebar);
  };

  const toggleMobileNav = () => {
    setShowMobileNav(!showMobileNav);
  };

  return (
    <div className="flex h-screen bg-gray-50 text-gray-800 overflow-hidden">
      {/* Sidebar Navigation */}
      {showMobileNav && (
        <div className="fixed inset-0 bg-gray-800 bg-opacity-75 z-40" onClick={toggleMobileNav}></div>
      )}
      
      <div className={`${showMobileNav ? 'fixed inset-y-0 left-0 z-50 w-64' : 'hidden'} md:flex md:flex-col md:w-16 bg-gray-900 text-white transition-all duration-300 ease-in-out`}>
        <div className="flex items-center justify-center h-16 border-b border-gray-800">
          <div className="w-8 h-8 rounded-md bg-blue-500 flex items-center justify-center">
            <MessageSquare size={20} />
          </div>
        </div>
        <div className="flex flex-col items-center py-4 space-y-6">
          <button className="w-10 h-10 rounded-md bg-blue-500 flex items-center justify-center text-white hover:bg-blue-600 transition-colors">
            <MessageSquare size={20} />
          </button>
          <button className="w-10 h-10 rounded-md hover:bg-gray-800 flex items-center justify-center">
            <BarChart2 size={20} />
          </button>
          <button className="w-10 h-10 rounded-md hover:bg-gray-800 flex items-center justify-center">
            <Users size={20} />
          </button>
          <button className="w-10 h-10 rounded-md hover:bg-gray-800 flex items-center justify-center">
            <Settings size={20} />
          </button>
        </div>
      </div>

      {/* Main Content */}
      <div className="flex-1 flex flex-col md:flex-row overflow-hidden">
        {/* Conversation List */}
        {showSidebar && (
          <div className="w-full md:w-80 border-r border-gray-200 flex flex-col">
            <div className="h-16 border-b border-gray-200 flex items-center justify-between px-4">
              <h2 className="text-lg font-semibold">Inbox</h2>
              <div className="flex items-center space-x-2">
                <button className="p-2 hover:bg-gray-100 rounded-full">
                  <Search size={18} />
                </button>
                <button className="p-2 hover:bg-gray-100 rounded-full">
                  <Bell size={18} />
                </button>
              </div>
            </div>
            <div className="flex items-center justify-between px-4 py-3 border-b border-gray-200">
              <div className="flex items-center space-x-2">
                <span className="text-sm font-medium">All conversations</span>
                <ChevronDown size={16} />
              </div>
              <span className="text-xs text-gray-500">5 conversations</span>
            </div>
            <div className="flex-1 overflow-y-auto">
              {dummyConversations.map((conversation) => (
                <div 
                  key={conversation.id}
                  className={`px-4 py-3 border-b border-gray-200 cursor-pointer hover:bg-gray-50 transition-colors ${selectedConversation?.id === conversation.id ? 'bg-blue-50' : ''}`}
                  onClick={() => handleSelectConversation(conversation)}
                >
                  <div className="flex items-start space-x-3">
                    <Avatar initials={conversation.avatar} status={conversation.status} />
                    <div className="flex-1 min-w-0">
                      <div className="flex items-center justify-between">
                        <h3 className="text-sm font-medium truncate">{conversation.name}</h3>
                        <span className="text-xs text-gray-500">{conversation.time}</span>
                      </div>
                      <p className="text-xs text-gray-500 truncate">{conversation.email}</p>
                      <p className={`text-sm truncate ${conversation.unread ? 'font-medium' : 'text-gray-500'}`}>
                        {conversation.lastMessage}
                      </p>
                    </div>
                    {conversation.unread && (
                      <div className="w-2 h-2 rounded-full bg-blue-500 mt-2"></div>
                    )}
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* Chat Interface */}
        <div className="flex-1 flex flex-col">
          {/* Chat Header */}
          <div className="h-16 border-b border-gray-200 flex items-center justify-between px-4">
            <div className="flex items-center">
              {isMobile && (
                <button onClick={toggleSidebar} className="mr-3 md:hidden">
                  {showSidebar ? <X size={20} /> : <Menu size={20} />}
                </button>
              )}
              <div className="hidden md:block md:mr-3">
                <button onClick={toggleSidebar} className="hover:bg-gray-100 p-1 rounded-md">
                  {showSidebar ? <ChevronLeft size={20} /> : <ChevronRight size={20} />}
                </button>
              </div>
              {selectedConversation ? (
                <div className="flex items-center space-x-3">
                  <Avatar initials={selectedConversation.avatar} status={selectedConversation.status} />
                  <div>
                    <h3 className="font-medium">{selectedConversation.name}</h3>
                    <p className="text-xs text-gray-500">{selectedConversation.email}</p>
                  </div>
                </div>
              ) : (
                <h3 className="font-medium">Select a conversation</h3>
              )}
            </div>
            <div className="flex items-center space-x-2">
              <button onClick={() => setShowAIPanel(!showAIPanel)} className={`p-2 rounded-md transition-colors ${showAIPanel ? 'bg-blue-100 text-blue-600' : 'hover:bg-gray-100'}`}>
                <Zap size={18} />
              </button>
              <button className="p-2 hover:bg-gray-100 rounded-md">
                <MoreHorizontal size={18} />
              </button>
            </div>
          </div>

          {/* Chat Messages */}
          <div className="flex-1 overflow-y-auto p-4 space-y-4">
            {selectedConversation ? (
              messages.map((message) => (
                <div key={message.id} className={`flex ${message.sender === 'user' ? 'justify-start' : 'justify-end'}`}>
                  <div className={`flex max-w-xs md:max-w-md space-x-2 ${message.sender === 'user' ? '' : 'flex-row-reverse space-x-reverse'}`}>
                    <div className="flex-shrink-0 mt-1">
                      <Avatar initials={message.avatar} />
                    </div>
                    <div>
                      <div className={`p-3 rounded-lg ${message.sender === 'user' ? 'bg-gray-100' : 'bg-blue-100 text-blue-900'}`}>
                        <p className="text-sm">{message.message}</p>
                      </div>
                      <p className="text-xs text-gray-500 mt-1">{message.time}</p>
                    </div>
                  </div>
                </div>
              ))
            ) : (
              <div className="flex items-center justify-center h-full">
                <p className="text-gray-500">Select a conversation to start chatting</p>
              </div>
            )}
          </div>

          {/* AI Response Panel */}
          {showAIPanel && selectedConversation && (
            <div className="border-t border-gray-200 bg-gray-50 p-3">
              <div className="flex items-center space-x-2 mb-2">
                <Bot size={16} className="text-blue-600" />
                <h4 className="text-sm font-medium text-blue-600">AI suggested responses</h4>
              </div>
              <div className="flex flex-col space-y-2">
                {aiSuggestions.map((suggestion, index) => (
                  <button 
                    key={index}
                    onClick={() => handleAIResponseSelect(suggestion)}
                    className={`p-2 text-sm text-left rounded hover:bg-blue-50 transition-colors ${selectedAIResponse === suggestion ? 'bg-blue-50 border border-blue-200' : 'bg-white border border-gray-200'}`}
                  >
                    {suggestion}
                  </button>
                ))}
              </div>
              <div className="flex justify-end mt-2">
                <div className="flex items-center space-x-2">
                  <button className="p-1 hover:bg-gray-200 rounded">
                    <ThumbsUp size={14} className="text-gray-500" />
                  </button>
                  <button className="p-1 hover:bg-gray-200 rounded">
                    <ThumbsDown size={14} className="text-gray-500" />
                  </button>
                </div>
              </div>
            </div>
          )}

          {/* Message Input */}
          {selectedConversation && (
            <div className="border-t border-gray-200 p-3">
              <div className="flex items-end space-x-2">
                <div className="flex-1 border border-gray-300 rounded-lg">
                  <textarea
                    value={newMessage}
                    onChange={(e) => setNewMessage(e.target.value)}
                    placeholder="Type your message..."
                    className="w-full p-3 rounded-lg resize-none focus:outline-none"
                    rows="3"
                  ></textarea>
                  <div className="flex items-center justify-between p-2 border-t border-gray-200">
                    <div className="flex items-center space-x-2">
                      <button className="p-1 hover:bg-gray-100 rounded">
                        <Paperclip size={18} className="text-gray-500" />
                      </button>
                      <button className="p-1 hover:bg-gray-100 rounded">
                        <SmilePlus size={18} className="text-gray-500" />
                      </button>
                    </div>
                    <div className="text-xs text-gray-500">
                      <span>Shift + Enter for new line</span>
                    </div>
                  </div>
                </div>
                <button 
                  onClick={handleSendMessage}
                  disabled={newMessage.trim() === ''}
                  className={`p-3 rounded-lg ${newMessage.trim() ? 'bg-blue-500 hover:bg-blue-600 text-white' : 'bg-gray-200 text-gray-500'} transition-colors`}
                >
                  <Send size={18} />
                </button>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}